    flex-direction: column;
    border-radius: 1.5rem;
    margin-right: 5rem;
    height: 300px;
    margin-bottom: 1rem;
}

.card{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;
}

.card h1,p{
    color: white;
    margin: .5rem 0px;
}